:root {
  --aqua-marin: #54e6af;
  --pale-cyan: #b3ffe2;
  --dark-blue-grey: #2c344b;
  --mirage: #121725;
  --white: #ffffff;
  --marble-blue: #5a668a;
  --light-blue-grey: #c2cbe5;
  --red: #fb3e3e;
}

.text-aqua-marin {
  color: var(--aqua-marin);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Chivo", sans-serif;
  font-weight: 300;
  min-height: 100vh;
  background: bottom 10% right/14.5rem 6.5rem no-repeat url(/assets/desktop/bg-pattern-dots.svg), right/55.5rem 40rem no-repeat url(/assets/desktop/image-host.jpg), var(--mirage);
}

.title {
  font-size: 3.25rem;
  font-weight: 300;
  line-height: 3.875rem;
  text-transform: uppercase;
  color: var(--white);
}
@media (max-width: 87.4375em) {
  .title {
    font-size: 3rem;
  }
}

body {
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 1.75rem;
  color: var(--light-blue-grey);
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  padding-top: 6.375rem;
  padding-bottom: 6.4375rem;
}

.wrapper {
  width: 45.1875rem;
  height: 31.8125rem;
  background: var(--mirage);
  padding-top: 5.5rem;
}
.wrapper .title {
  margin-bottom: 1.5rem;
}
.wrapper .text {
  width: 27.8125rem;
  margin-bottom: 2.5rem;
}
.wrapper .form {
  margin-bottom: 4rem;
}
.wrapper .form.invalid {
  margin-bottom: 1.75rem;
}
.wrapper .companies {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 33.5rem;
}

.button {
  font-family: inherit;
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--mirage);
  padding: 0.5625rem 1.6875rem;
  background: var(--aqua-marin);
  border: none;
  width: 10.125rem;
  height: 2.875rem;
  border-radius: 1.75rem;
  cursor: pointer;
}
.button:hover {
  background: var(--pale-cyan);
}

.form {
  width: 26.6875rem;
  position: relative;
}
.form .email-input {
  width: 100%;
  height: 3.5rem;
  padding: 0.875rem 10.625rem 0.875rem 2rem;
  background: var(--dark-blue-grey);
  color: var(--white);
  border: none;
  outline: none;
  border-radius: 1.75rem;
}
.form .email-input::placeholder {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--light-blue-grey);
}
.form .button {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-0.3125rem, 0.3125rem);
}
.form.invalid:after {
  display: block;
  content: "Oops! Please check your email";
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--red);
  text-indent: 2rem;
  margin-top: 0.5rem;
}